.app-body {
  background-color: #1c1415;
  color: #a3908e;
}

.container {
    display: flex;
    flex-direction: column;
    width: 90vw;
    margin-left: 5vw;
    margin-right: 5vw;
    margin-bottom: 10vh;
    gap: 10vh;
}

.stat-container {
    border: 1px solid #812421;
    border-radius: 8px;
    display: flex;
    min-width: 25vw;
    height: 130px;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    margin: 5px;
}

.Stat-Oval {
  margin-top: 0.9em;
  display: flex;
  border: 1px solid #812421;
  background-color: #1c1415;
  width: 80px;
  height: 40px;
  border-radius: 100px / 50px;
  align-items: center;
  justify-content: center;
}

.stat-container > p {
  font-size: 0.8em;
  font-weight: 700;
  margin-top: 1em;
  margin-bottom: 0;
}


.stat-container > h2 {
  border: 0.5px solid #ce9d63;
  border-radius: 8px;
  padding: 0 25px 0 25px;
  font-size: 2em;
  margin-bottom: 0;
  margin-top: 0.5em;
}

.stat-container > h3 {
  font-size: 1.5em;
  margin-bottom: 0;
}

.stat-button-container {
  margin: 0.3em 0 2em 0;
  z-index: 99;
  padding-bottom: 2em;
}

.stat-button {
  color: #812421;
  border: 1px solid #812421;
  width: 40px;
  height: 40px;
  background-color: #ce9d63;
  border-radius: 8px;
  font-size: 1em;
  font-weight: 600;
}

.stat-button-small {
  color: #812421;
  border: 1px solid #812421;
  background-color: #ce9d63;
  border-radius: 4px;
  font-size: 1em;
  font-weight: 600;
  align-self: center;
  justify-self: center;
  width: 20px;
  height: 20px;
  padding: 4px 4px;
}

.row {
  gap: 5vw;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.skills-container {
    display: flex;
    width: 100vw;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    gap: 2em;
}

table {
    font-family: arial, sans-serif;
    border-collapse: collapse;
    width: 90vw;
    margin-left: 5vw;
    margin-right: 5vw;
    margin-top: 2em;
  }
  
  td, th {
    border: 0;
    border-bottom: 0.5px solid #dddddd;
    text-align: left;
    padding: 8px;
  }

  .Stat-Name {
    color: #ce9d63;
    font-weight: 600;
    font-size: 1em;
  }

  .Stat-Border {
    text-align: center;
    width: 2em;
    margin: 0;
    font-size: 1em;
    border: 0.5px solid #ce9d63;
    border-radius: 4px;
    padding: 0 8px 0 8px;
  }

  .Round-Checkbox {
    appearance: none;
    background-color: transparent;
    cursor: pointer;
    width: 1.15em;
    height: 1.15em;
    border: 0.1em dashed white;
    border-radius: 50%;
    transform: translateY(-0.075em);
    display: grid;
    place-content: center;
    transition: 0.3s ease-out;
    opacity: 70%;
  }

  .Round-Checkbox:checked {
    opacity: 100%;
    background-color: white;
    border: none;
    width: 1.15em;
    height: 1.15em;
    transition: 0.3s ease-in;
    box-shadow: inset 1em 1em var(--form-control-color);
  }

  .header{
    display: flex;
    flex-direction: column;
    gap: 2em;
    width: 90vw;
    margin-left: 5vw;
    margin-right: 5vw;
  }

  .Header-Top {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
    justify-content: space-between;
    margin-top: 1em;
  }


  .Header-Top-Left > h5 {
    margin: 0;
  }

  .Header-Top-Right {
    display: flex;
    flex-direction: column;
    gap: 1em;
    align-items: center;
    justify-content: center;
  }

  .Header-Top-Right > .Health-Button {
    border: 1px solid #812421;
    background-color: transparent;
    border-radius: 8px;
    padding: 0.5em 1.5em;
  }

  .Header-Top-Right > .Health-Button > h3 {
    color: #ce9d63;
    margin: 0;
  }

  .Header-Top-Right > .Health-Button > h5 {
    color: #a3908e;
    margin: 0;
  }

  .Header-Top-Right > .Settings-Button {
    font-size: 1.5em;
    color: white;
    background-color: transparent;
    border: none;
  }

  .Header-Top-Right > .Settings-Button:active {
    animation: zoom-in-zoom-out 1s ease;
  }

  .Header-Top-Right > .Settings-Button:hover {
    color: #D6E4E5;
  }

  @keyframes zoom-in-zoom-out {
    0% {
      transform: scale(1);
    }
    50% {
      transform: scale(1.5);
    }
    100% {
      transform: scale(1);
    }
  }

  .Header-Mid {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
  }

  .Header-Mid > img {
    width: 100px;
    height: 100px;
    object-fit: cover;
    background-color: teal;
  }

  .bottom-stats {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 2em;
  }

  .bottom-stats-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .bottom-stats-container p {
    margin-top: 0;
    font-size: 0.7em;
    font-weight: 700;
  }

  .Health-Form {
    display: none;
    position: absolute;
    width: 90vw;
    height: 90vh;
    background-color: white;
  }

  .Saving-Throws{
      display: flex;
      width: 100vw;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      gap: 1em;
  }

  .Saving-Throw {
    border: 1px solid #812421;
    border-radius: 8px;
    display: flex;
    min-width: 40vw;
    height: 50px;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
    margin: 5px;
    font-size: 0.8em;
  }

  .Saving-Throw > .Round-Checkbox {
    margin: -0.5em;
    opacity: 80%;
    background-color: #1c1415;
  }
  .Saving-Throw > .Round-Checkbox:checked {
    background-color: white;
  }

  .Saving-Throw > .Stat-Border {
    width: 1em;
    padding: 8px 8px;
    margin-right: 1em;
  }

  .Section-Label{
    margin-left: 5vw;
  }

  hr {
    width: 90vw;
    border: 0;
    border-bottom: 0.5px solid #dddddd;
  }

  .Sense {
    border: 1px solid #812421;
    border-radius: 8px;
    display: flex;
    max-width: 90vw;
    height: 50px;
    align-items: center;
    justify-content: left;
    flex-direction: row;
    margin-left: 5vw;
    margin-bottom: 2em;
    font-size: 0.8em;
    gap: 2em;
  }

  .Sense > h2 {
    margin-left: 1em;
  }

  .Sense > .Stat-Border {
    width: 2em;
    padding: 10px 10px;
    margin-right: 1em;
  }